@import "../Root.scss";

#tambah_kasir {
  margin-left: 1rem;
  @media screen and (min-width: $break-large) {
    .title {
      font-size: 64px !important;
    }

    .subtitle {
      font-size: 36px !important;
    }
  }

  @media screen and (min-width: $break-medium) {
    .title {
      font-size: 48px;
    }

    .subtitle {
      font-size: 32px;
    }
  }

  .title {
    font-family: $font-header;
  }

  .subtitle {
    font-family: $font-sub;
    font-weight: 500;
  }

  .center {
    display: flex;
    justify-content: center;
  }
}
